/// <reference path="../_references.ts"/>

module powerbi.visuals {
    // I support a categorical (ordinal) X with measure Y for a single series
    export const dataDotChartCapabilities: VisualCapabilities = {
        dataRoles: [
            {
                name: 'Category',
                kind: VisualDataRoleKind.Grouping,
                displayName: data.createDisplayNameGetter('Role_DisplayName_Axis'),
                description: data.createDisplayNameGetter('Role_DisplayName_AxisDescription')
            }, {
                name: 'Y',
                kind: VisualDataRoleKind.Measure,
                displayName: data.createDisplayNameGetter('Role_DisplayName_Value'),
                requiredTypes: [{ numeric: true }, { integer: true }]
            },
        ],
        objects: {
            general: {
                displayName: data.createDisplayNameGetter('Visual_General'),
                properties: {
                    formatString: StandardObjectProperties.formatString,
                },
            },
        },
        dataViewMappings: [{
            conditions: [
                { 'Category': { max: 1 }, 'Y': { max: 1 } }
            ],
            categorical: {
                categories: {
                    for: { in: 'Category' },
                    dataReductionAlgorithm: { top: {} }
                },
                values: {
                    select: [{
                        for: { in: 'Y' },
                    }]
                },
            },
        }]
    };
} 


