/// <reference path="../_references.ts"/>

module powerbi.visuals.services {

    export function createGeolocation(): IGeolocation {
        return new GeolocationService();
    }

    /**
     * HTML5 Implementation of IGeolocation
     */
    class GeolocationService implements IGeolocation {
        private webGeolocation: Geolocation;
        
        constructor() {
            this.webGeolocation = navigator.geolocation;
        }
        
        public watchPosition(successCallback: IPositionCallback, errorCallback?: IPositionErrorCallback): number {
            return this.webGeolocation.watchPosition(
                (position: Position) => {
                    successCallback(position);
                },
                (error: PositionError) => {
                    if (errorCallback != null) {
                        errorCallback(error);
                    }
                }
            );
        }

        public clearWatch(watchId: number): void {
            this.webGeolocation.clearWatch(watchId);
        }

        public getCurrentPosition(successCallback: IPositionCallback, errorCallback?: IPositionErrorCallback): void {
            this.webGeolocation.getCurrentPosition(
                (position: Position) => {
                    successCallback(position);
                },
                (error: PositionError) => {
                    if (errorCallback != null) {
                        errorCallback(error);
                    }
                }
            );
        }

    }
}


